<template>
  <div class="chat-container">
    <!--  -->
    <van-nav-bar
      class="page-navbar"
      title=""
      left-arrow
      @click-left="$router.back()"
      fixed
    />
    <!-- / -->

    <!--  -->
    <div class="message-list" ref="message-list">
      <div
        class="message-item"
        v-for="(item, index) in messages"
        :key="index"
      >
        <van-image
          class="avatar"
          round
          fit="cover"
          :src="item.photo"
        />
        <div class="title">{{ item.msg }}</div>
      </div>
    </div>
    <!-- / -->

    <!--  -->
    <van-cell-group class="send-message">
      <van-field
        v-model="message"
        center
        clearable
      >
        <van-button
          slot="button"
          size="small"
          type="primary"
          @click="onSend"
        ></van-button>
      </van-field>
    </van-cell-group>
    <!-- / -->
  </div>
</template>

<script>
import io from 'socket.io-client'
import { getItem, setItem } from '@/utils/storage'

export default {
  name: 'ChatPage',
  data () {
    return {
      message: '',
      socket: null,
      messages: getItem('messages') || [] // 
    }
  },

  watch: {
    messages () {
      setItem('messages', this.messages)

      this.scrollToBottom()
    }
  },

  created () {
    const socket = io('http://ttapi.research.itcast.cn')
    this.socket = socket

    // 
    // socket.emit('message', '')

    socket.on('message', data => {
      console.log('', data)
      data.photo = 'http://toutiao.meiduo.site/FhWyOsSDIAIlEZj6tTGvO2aTizha'
      this.messages.push(data)
    })
  },

  mounted () {
    //  window 
    // window.list = this.$refs['message-list']
    this.scrollToBottom()
  },

  methods: {
    onSend () {
      // socket.emit('message', '')
      const message = this.message.trim()

      // 
      if (!message) {
        return
      }

      // 
      // this.socket.emit('message', this.message)

      // 
      // Body 
      const data = {
        msg: message,
        timestamp: Date.now()
      }
      this.socket.emit('message', data)

      // 
      data.isMe = true
      // 
      data.photo = 'https://img.yzcdn.cn/vant/cat.jpeg'
      this.messages.push(data)

      // 
      this.message = ''
    },

    scrollToBottom () {
      const messageList = this.$refs['message-list']
      this.$nextTick(() => {
        messageList.scrollTop = messageList.scrollHeight
      })
    }
  }
}
</script>

<style scoped lang="less">
.chat-container {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 46px 0 50px 0;
  top: 0;
  left: 0;
  box-sizing: border-box;
  background: #f5f5f6;
  .message-list {
    height: 100%;
    overflow-y: scroll;
    .message-item {
      display: flex;
      flex-flow: row wrap;
      align-items: center;
      padding: 10px;
      .title {
        background: #fff;
        padding: 5px;
        border-radius: 6px;
        font-size: 14px;
      }
      .avatar {
        width: 40px;
        height: 40px;
        margin-right: 5px;
      }
    }
    .reverse {
      flex-direction: row-reverse;
      .title {
        margin-right: 5px;
      }
    }
  }

  .send-message {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: #f5f5f6 !important;
  }
}
</style>
